/**
 * adopted from https://github.com/airbnb/rheostat
 */

export const geometric = {
  getPosition (value, min, max) {
    return (max / (max - min)) ** 0.5 * ((value - min) / max) ** 0.5 * 100
  },

  getValue (percentageDiff, min, max) {
    return Math.round((percentageDiff / 100) ** 2 * (max - min)) + min
  }
}

export const linear = {
  getPosition (value, min, max) {
    return ((value - min) / (max - min)) * 100
  },

  getValue (percentageDiff, min, max) {
    const decimal = percentageDiff / 100

    if (percentageDiff <= 0) {
      return min
    } else if (percentageDiff >= 100) {
      return max
    }

    return Math.round((max - min) * decimal + min)
  }
}

export const log = {
  getPosition (value, min, max) {
    const minv = min === 0 ? 0 : Math.log(min)
    const maxv = Math.log(max)

    const scale = (maxv - minv) / 100

    return (Math.log(value) - minv) / scale
  },

  getValue (percentageDiff, min, max) {
    const minv = min === 0 ? 0 : Math.log(min)
    const maxv = Math.log(max)

    if (percentageDiff <= 0) {
      return min
    } else if (percentageDiff >= 100) {
      return max
    }

    const scale = (maxv - minv) / 100

    return Math.floor(Math.exp(minv + scale * percentageDiff))
  }
}
